.page-container {
    margin: 0 auto;
    padding: 0;
    text-align: center;
    position: relative;
}

.image-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
    z-index: -1;
    margin: 0;
    padding: 0;
}

.page-container .page-title {
    font-family: "Italiana", "Gill Sans";
    padding-top: 10%;
}

#show-of-hearts-title {
    color: #ce0033;
}

.date {
    color: #fff;
    font-family: "Italiana", "Gill Sans";
}


.media-art-section {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 40px;
    margin-top: 30px;
}

.media-art-item {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    gap: 10px;
    padding-bottom: 10%;
    min-height: 50vh;
}

.media-art-item.left {
    flex-direction: row;
}

.media-art-item.right {
    flex-direction: row-reverse;
}

.media-art-item iframe {
    width: 60vw;
    min-height: 400px;
    /* border-radius: 10px; */
}

.media-art-item img {
    width: auto;
    height: 400px;
    /* border-radius: 10px; */
}

.page-text {
    font-size: 1.1rem;
    color: white;
    text-align: left;
    /* margin: 20px; */
    font-family: "Lato", sans-serif;
    font-weight: 300;
    max-width: 60vw;
    margin: 0 auto;
}

.media-description h2 {
    color: #e63b6b;
    font-family: "Italiana", sans-serif;
    letter-spacing: 0.1rem;
    padding: 0;
    margin-bottom: 5px;
}

.media-description span {
    color: white;
    font-weight: bold;
    font-family: "Lato", sans-serif;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    font-size: 0.9rem;
}

.images {
    /* display: flex; */
    /* justify-content: space-around; */
    /* gap: 10px; */
    padding: 50px;
}

.images img {
    width: 30vw;
    height: auto;
    margin: 10px;
    border: solid white 15px;
}

@media (max-width: 768px) {
    .page-container .page-title {
        font-size: 2rem;
    }

    .images img {
        width: 60vw;
        height: auto;
        margin: 10px;
    }


    .page-text {
        font-size: 1rem;
        max-width: 80vw;
    }
}