body, html {
  margin: 0;
  padding: 0;
}

.photography-container {
  background-color: black;
  margin: 0;
  padding: 0;
}

.photography-container .page-title {
  margin: 0;
  padding: 100px 0;
  text-align: center;
  color: white;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.column {
  flex: 25%;
  max-width: 25%;
  padding: 0;
  margin: 0;
}

.column img {
  display: block;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
  vertical-align: top;
}

/* Responsive */
@media screen and (max-width: 800px) {
  .column {
    flex: 30%;
    max-width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .column {
    flex: 50%;
    max-width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .column {
    flex: 100%;
    max-width: 100%;
  }
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal img {
  /* width: 80vw; */
  height: 80vh;
  max-height: 90vh;
}
