.page-container {
    /* max-width: 1200px; */
    margin: 0 auto;
    padding: 0;
    text-align: center;
    /* background-color: black; */
    padding: 4rem 1rem;
}

#as-a-witness-container {
    background-color: black;
}

.page-container .page-title {
    font-family: "Italiana", "Gill Sans";
    padding-top: 0;
}

.subheading {
    color: #fff;
    font-family: "Italiana", "Gill Sans";
}

.intro-text {
    width: 60vw;
    font-size: 1rem;
    color: white;
    text-align: left;
    /* margin: 20px; */
    font-family: "Lato", sans-serif;
    font-weight: 300;
    margin: 0 auto;
}

.media-images {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.media-images img{
   width: 30vw;
   border: solid white 1px;
   margin: 20px;
}

.media-art-section {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 40px;
    margin-top: 30px;
}

.media-art-item {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    gap: 10px;
    padding-bottom: 10%;
    min-height: 50vh;
}

.page-container .media-description {
    width: 60vw;
    font-size: 1rem;
    color: white;
    text-align: left;
    /* margin: 20px; */
    font-family: "Lato", sans-serif;
    font-weight: 300;
    margin: 0 auto;
}

.media-description h2 {
    color: #e63b6b;
    font-family: "Italiana", sans-serif;
    letter-spacing: 0.1rem;
    padding: 0;
    margin-bottom: 5px;
}

@media (max-width: 768px) {
    .page-container .media-description {
        width: 80vw;
    }

    .media-art-item {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .intro-text {
        width: 80vw;
    }

    .media-images img{
        width: 50vw;
     }
}