/* Container wrapping everything */
.home-container {
    position: relative;
    width: 100%;
    height: 100%;
}

.bg-video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* object-fit: cover; */
    z-index: -2;
    background-color: black;
}

.bg-video .text {
    color: white;
}

#content {
    position: sticky;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    color: white;
    margin: 0;
    min-height: 100vh;
}

#content1 {
    position: absolute;
    top: 50vh;
    left: 0;
    z-index: -1;
    /* width: 50vw; */
}

#content2 {
    position: absolute;
    top: 200vh;
    left: 0;
    z-index: -1;
    /* width: 50vw; */
}


#content3 {
    position: absolute;
    top: 300vh;
    left: 0;
    z-index: -1;
    /* width: 50vw; */
}


/* .content p {
    background-color: rgba(0, 0, 0, 0.5);
    padding: 10px 10%;
