#container {
    /* Center the text in the viewport. */
    position: absolute;
    margin: auto;
    width: 100vw;
    height: 30%;
    top: 0;
    bottom: 0;

    /* This filter is a lot of the magic, try commenting it out to see how the morphing works! */
    filter: url(#threshold) blur(0.6px);
}

/* Your average text styling */
#text1,
#text2 {
    position: absolute;
    width: 100%;
    display: inline-block;

    font-family: "Comfortaa", 'Lato', sans-serif;
    font-size: 8rem;

    text-align: center;

    user-select: none;
}

@media (max-width: 768px) {

    #text1,
    #text2 {
        font-size: 4rem;
    }
}



/* ABOUT SECTION */
.about-container {
    background-color: black;
    color: white;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: -10;
    padding: 10%;
    gap: 50px;
}

.about-container h2 {
    font-family: "Italiana", sans-serif;
    font-size: 3rem;
    color: #e63b6b;
}

.about-container .text {
    /* padding: 5rem 5rem 0 5rem; */
    max-width: 400px;
    font-family: "Lato";
}

.about-container p {
    font-weight: 300;
}

.about-container img {
    width: 50vw;
    max-width: 400px;
    height: auto;
    /* padding: 5rem; */
}

@media (max-width: 768px) {
    .about-container img {
        width: 80vw;
        max-width: 400px;
    }
}

#video-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    z-index: -1;
    transition: opacity 0.5s ease;
    background-color: black;
}

#video-background iframe {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100vw;
    height: 56.25vw;
    /* 16:9 aspect ratio */
    min-height: 100vh;
    min-width: 177.77vh;
    /* 16:9 aspect ratio */
    transform: translate(-50%, -50%);
}

.video-container {
    width: 100%;
    aspect-ratio: 16 / 10;
}

.video {
    width: 100%;
    height: 100%;
}